import React, { Component } from "react";
import PropTypes from "prop-types";
import clsx from 'clsx';
import { withStyles, useTheme } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import Logo from "../assets/images/logo.png";
import Menu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import Avatar from "@material-ui/core/Avatar";
import Container from "@material-ui/core/Container";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import {withRouter} from 'react-router-dom'
import {Link} from 'react-router-dom'

const styles = (theme) => ({
  root: {
    flexGrow: 1,
  },
  // menuButton: {
  //   marginRight: theme.spacing(2),
  // },
  appbar: {
    boxShadow: "0px -6px 14px rgba(0, 0, 0, 0.33)",
    position:"relative",  
  },
  Toolbar:{
    [theme.breakpoints.up('lg')]: {
      width: 1240,
      margin: "0 auto",
    }
  },
  profileBtn: {
    marginLeft: "auto",
    textTransform: "capitalize",
    fontFamily: `Montserrat-Regular`,
    fontSize: 14,
    paddingRight:0,
    paddingLeft:0,
  },
  avatar: {
    backgroundColor: "#4A4A4A",
    marginRight: 5,
    fontSize:14,
    height:25,
    width:25,
  },
  logo:{
    marginTop:6,
  }
});

class login extends Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
  };
  constructor(props) {
    super(props);
    this.state = {
      anchorEl: null,
    };
  }

  handleClick = (event) => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };
  logout=()=>{
    this.setState({ anchorEl: null });
    localStorage.removeItem("status")
    this.props.history.push("/login")
  }
  render() {
    const { classes } = this.props;
    return (
      <>
        <AppBar position="static" className={classes.appbar} color="inherit">
          <Toolbar className={classes.Toolbar}>
            {/* <Button
              className={classes.menuButton}
              color="inherit"
              aria-label="menu"
            > */}
            <Link to="/">
              <img className={classes.logo} src={Logo} width="200px"/>
              </Link>
            {/* </Button> */}
            {
              localStorage.getItem("status") === "success" ?
            <Button
              aria-controls="simple-menu"
              className={classes.profileBtn}
              aria-haspopup="true"
              onClick={this.handleClick}
            >
              <Avatar size="small" className={classes.avatar}>N</Avatar>
              Neeraj Aggarwala <ExpandMoreIcon />
            </Button>
            : ""
  }
            <Menu
              id="simple-menu"
              className="nav-dropdown"
              anchorEl={this.state.anchorEl}
              keepMounted
              open={Boolean(this.state.anchorEl)}
              onClose={this.handleClose}
            >
              <MenuItem onClick={this.handleClose}>Profile</MenuItem>
              <MenuItem onClick={this.handleClose}>My account</MenuItem>
              <MenuItem onClick={this.logout}>Logout</MenuItem>
            </Menu>
          </Toolbar>
        </AppBar>
      </>
    );
  }
}
export default withStyles(styles, { withTheme: true })(withRouter(login));
