/* eslint-disable no-use-before-define */
import React,{useEffect} from 'react';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import {userServices} from '../_userServices/userServices'
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

export default function ComboBox(props) {
    const [state,setState]=React.useState({
      keyword:"delhi",
      data:null,
    });
    const onChange=(e)=>{
        if(props.state === "service_partner"){
          setState({...state,data:null})
        userServices
        .noti_type("subtype",e.target.value)
        .then((res)=>(
          setState({...state,data:res.data})
        ))
        }
   
    }
console.log(state.data)
  return (
    <Autocomplete
      className="input-oulined notification2"
      onChange={(e,value)=>props.landingSearch(value)}
      popupIcon={<ExpandMoreIcon/>}
      id="locality"
      options={state.data === null || state.data === "" ? []: state.data}
      getOptionLabel={options=>  props.state === "service_partner" ? options.subtype_name : props.state==="service_landing" ? options.subtype : options.sport_name}
      renderInput={(params) => <TextField placeholder="Select"  onChange={onChange} {...params} className="input-oulined" variant="outlined" />
  }
    />
  );
}

