/* eslint-disable no-use-before-define */
import React,{useEffect} from 'react';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';
import {userServices} from '../_userServices/userServices'
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

export default function ComboBox(props) {
    const [state,setState]=React.useState({
keyword:"delhi",
data:"",
    });
    const onChange=(e)=>{
        userServices
        .locality(e.target.value)
        .then((res)=>(
          setState({...state,data:res.data})
        ))
    }
  return (
    <Autocomplete
    className="input-oulined notification2"
    onChange={(e,value)=>props.locality(value)}
      id="locality"
      popupIcon={<ExpandMoreIcon/>}
      options={state.data === "" ? []: state.data}
      getOptionLabel={(option) => option.locality}
      renderInput={(params) => <TextField placeholder="Select Locality" onChange={onChange} {...params} className="input-oulined"
      defaultValue="delhi" variant="outlined" />}
    />
  );
}
