const Slots = (props) => {
  const [age, setAge] = React.useState("");
  const handleChange = (event) => {
    setAge(event.target.value);
  };
 
  const [state, setState] = React.useState({});
  const onChangeRadio=(value,e,val)=>{
    setState({...state, [value]:val})
  }
  const add_minutes =  function (dt, minutes) {
    return new Date(dt.getTime() + minutes*60000);
}
const add_minutes2 =  function (dt, minutes) {
  return new Date(dt.getTime() + (minutes + minutes) * 60000);
}
 
  const [selectedDate, updateState] = React.useState({
    firstDate:"",
    lastDate:"",
    minSlot:"",
    intervals:"",
    loading:false,
  });
  const handleDateChange=(date)=>{
    updateState({...selectedDate,firstDate:date._d})
  }
  const handleDateChangeLast=(date)=>{
    updateState({...selectedDate,lastDate:date._d})
  }
  const onChangeIntervals=(e)=>{
    updateState({...selectedDate,intervals:e.target.value})
  }
  const onChangeMinSlots=(e)=>{
    updateState({...selectedDate,minSlot:e.target.value})
  }
  const onSubmit=()=>{
    updateState({...selectedDate,loading:true})
    userServices
    .setSlotTime(props.id,selectedDate)
    .then((res)=>{
      updateState({...selectedDate,loading:false})
    })
  }
  useEffect(() => {
    updateState({...selectedDate,loading:true})
    userServices
    .slotTime(props.id)
    .then((res)=>{
      const first_date = `Sat Jul 11 2020 ${res.data.time.slice(0,5)}:03 GMT+0500 (Pakistan Standard Time)`;
      setState({...state,data:res,first_date:add_minutes(new Date(first_date), res.data.slot_interval),first_date2:add_minutes2(new Date(first_date), res.data.slot_interval)})
      updateState({...selectedDate,intervals:res.data.slot_interval,minSlot:res.data.min_slots,loading:false})
  })
  
  },[]);
  return (
    <>
      <Box fontSize={16} mb={2} className="font-family-bold">
        Slots
      </Box>
      <Box
        bgcolor="#fff"
        className="intext-input"
        fontSize={12}
        boxShadow="0px 1px 4px rgba(0, 0, 0, 0.08)"
        borderRadius={4}
        width={320}
        p={2}
        mb={2}
      >
        {
          selectedDate.loading === true ? 
          <>
          <Grid container spacing={2}>
            <Grid item lg={6} sm={6} xl={6} xs={6}> <Skeleton variant="rect" height={40} width="100%"/></Grid>
            <Grid item lg={6} sm={6} xl={6} xs={6}> <Skeleton variant="rect" height={40} width="100%"/></Grid>
            </Grid>
            <Box mt={2}/>
            <Skeleton variant="rect" width="100%" height={40} />
            <Box mt={2}/>
            <Skeleton variant="rect" width="100%" height={35} />
            <Box mt={2}/>
            <Skeleton variant="rect" width="100%" height={40} />
            </>
            :
            <>
                  
     <Grid container spacing={1}>
          <Grid item lg={6}>
          <Box
          border="1px solid #DADADA"
          pt={0.8}
          mb={2}
          borderRadius={4}
          bgcolor="#fff"
          fontSize={9}
          className="input-oulined date-picker"
        >
          <span>Start Time</span>
          
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <TimePicker
                  autoOk
                  size="small"
                  value={selectedDate.firstDate === "" ? state.data === undefined ?  selectedDate.firstDate :`Tue Jul 21 2020 ${state.data.data.time.slice(0,5)}:03 GMT+0500 (Pakistan Standard Time)` : selectedDate.firstDate}
                  minutesStep={5}
                  onChange={handleDateChange}
                />
              </MuiPickersUtilsProvider>
            </Box>
          </Grid>
          <Grid item lg={6}>
          <Box
          border="1px solid #DADADA"
          pt={0.8}
          mb={2}
          borderRadius={4}
          bgcolor="#fff"
          fontSize={9}
          className="input-oulined date-picker"
        >
          <span>End Time</span>
          
              <MuiPickersUtilsProvider utils={MomentUtils}>
                <TimePicker
                  autoOk
                  showTodayButton
                  todayLabel="now"
                  size="small"
                  value={selectedDate.lastDate === "" ? state.data === undefined ?  selectedDate.lastDate :`Tue Jul 21 2020 ${state.data.data.time.slice(-5)}:03 GMT+0500 (Pakistan Standard Time)` : selectedDate.lastDate}
                  minutesStep={5}
                  onChange={handleDateChangeLast}
                />
              </MuiPickersUtilsProvider>
            </Box>
          </Grid>
        </Grid>

        <Box
          border="1px solid #DADADA"
          className="input-oulined"
          mb={2}
          pt={0.8}
          borderRadius={4}
          bgcolor="#fff"
          fontSize={9}
        >
          <span>Slot Intervals (minutes)</span>
          <TextField
            id="standard-basic"
            size="small"
            onChange={onChangeIntervals}
            value={selectedDate.intervals}
            // type="number"
            variant="outlined"
            placeholder="Service Name"
            fullWidth
          />
        </Box>
        <Box
          fontSize={9}
          mt={1}
          color="#4A4A4A"
          className="font-family-regular"
        >
          Example of Slots Created:
        </Box>
        <Box
          fontSize={10}
          mb={2}
          color="#4A4A4A"
          className="font-family-regular"
        >
          {`${state.data === undefined ? "" : `${state.data.data.time.slice(0,5)}-${ parseInt(state.first_date.getHours()) > 9 ? state.first_date.getHours() : `0${state.first_date.getHours()}`}:${parseInt(state.first_date.getMinutes()) > 9 ? state.first_date.getMinutes(): `0${state.first_date.getMinutes()}`}`}`} || {' '}
          {`${state.data === undefined ? "" : `${ parseInt(state.first_date.getHours()) > 9 ? state.first_date.getHours() : `0${state.first_date.getHours()}:${parseInt(state.first_date.getMinutes()) > 9 ? state.first_date.getMinutes(): `0${state.first_date.getMinutes()}`}`}-${ parseInt(state.first_date2.getHours()) > 9 ? state.first_date2.getHours() : `0${state.first_date2.getHours()}`}:${parseInt(state.first_date2.getMinutes()) > 9 ? state.first_date2.getMinutes(): `0${state.first_date2.getMinutes()}`}`}`}
        </Box>
        <Box
          border="1px solid #DADADA"
          className="input-oulined"
          mb={2}
          pt={0.8}
          borderRadius={4}
          bgcolor="#fff"
          fontSize={9}
        >
          <span>Minimum Slots in a Booking:</span>
          <TextField
            id="standard-basic"
            size="small"
            type="number"
            onChange={onChangeMinSlots}
            value={selectedDate.minSlot}
            // type="number"
            variant="outlined"
            placeholder="Service Name"
            fullWidth
          />
        </Box>
            </>
        }
        </Box>
   
      {
        selectedDate.loading === true ? <CircularProgress color="secondary" />:<Button onClick={onSubmit} disabled={(selectedDate.firstDate && selectedDate.lastDate)=== ""} className="btn-primary" > <Box px={3}>Save</Box>
        </Button>
      }
      
       
    </>
  );
};
