import React from 'react';
import clsx from 'clsx';
import {Tabs,Tab,Container,Box,Typography,AppBar} from '@material-ui/core';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import {Link} from 'react-router-dom';
import { withRouter } from "react-router";
import { de } from 'date-fns/locale';
function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <div
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && (
          <Box p={3}>
            <Typography>{children}</Typography>
          </Box>
        )}
      </div>
    );
  }
  
  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
  };
  
  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }
  
  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      backgroundColor: theme.palette.background.paper,
    },
    TopBar:{
        boxShadow:"none",
        borderBottom:"1px solid #D7D7D7",
    },
    tab:{
        padding:0,
        textTransform:"capitalize"
    }
  }));

function Services(props) {
    const classes = useStyles();
    const [value, setValue] = React.useState(props.tabvalue);

    const handleChange = (event, newValue) => {
      switch(newValue) {
        case 0:
          // props.history.push("")
          break;
        case 1:
          props.history.push("/")
          break;
          case 2:
            props.history.push("marketing")
            break;
            case 3:
              props.history.push("reports")
              break;
        default:
          setValue(newValue)
      }
    };
    
    return (
        <div>
                      
            <AppBar position="static" color="inherit" className={classes.TopBar}>
            <Container className="services-tab"> 
            <Tabs value={value} onChange={handleChange} aria-label="simple tabs example">
            <Tab className={clsx(classes.tab,"my-follow")} label="My Follow-ups" {...a11yProps(0)} />
           <Tab className={classes.tab} label="Listings" {...a11yProps(1)} />
           <Tab className={classes.tab} label="Marketing" {...a11yProps(2)} />
            <Tab className={classes.tab} label="Reports" {...a11yProps(3)} />
            <Tab className={classes.tab} label="Payouts" {...a11yProps(4)} />
            <Tab className={classes.tab} label="Staff" {...a11yProps(5)} />
            </Tabs>
            </Container>
            </AppBar> 
            
           
        </div>
    )
}
export default withRouter(Services);