import React, { useEffect } from 'react'
import Grid from '@material-ui/core/Grid'
import Box from '@material-ui/core/Box'
import IconButton from '@material-ui/core/IconButton'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import { userServices } from '../_userServices/userServices'
import Skeleton from '@material-ui/lab/Skeleton';
import EditIcon from '@material-ui/icons/Edit';
export default function Commissions(props) {
  const [state,setState] = React.useState({
    data:"",
    loading:true,
  })
  useEffect(() => {
    setState({...state,loading:true})
    userServices
    .commissions(props.id)
    .then((res)=>{
      setState({...state,data:res.data,loading:false});
    })
  }, [])
  const onchange=(value,e)=>{
    
    if(parseInt(value.commission) !== parseInt(e.target.value)){
    setState({...state,loading:true}); 
    userServices
    .commissionsUpdate(value,e)
    .then((res)=>{
      userServices
    .commissions(props.id)
    .then((res)=>{
      setState({...state,data:res.data,loading:false});
    })
    })
  }
  }
    return (
        <div className="intext-input">
            <Grid container>
                <Grid item xl={4} lg={4} md={4} sm={6} xs={12}>
                <Box fontSize={16} mb={2} className="font-family-bold">
          Commissions
        </Box>
        
           {state.loading === true ?
           <>
           {[1,2,3].map((value,index)=>(
 <Box
 key={index}
 bgcolor="#fff"
 boxShadow="0px 1px 4px rgba(0, 0, 0, 0.08)"
 borderRadius={4}
 p={2}
 mb={2}
>
  <Box mb={1.5}><Skeleton variant="rect" width="100%" height={18} /></Box>
  <Skeleton variant="rect" width="100%" height={40} />
  </Box>
  
           ))}
           </>
           
            
           
            :
            
              state.data.map((value,index)=>(
                <Box
                key={index}
          bgcolor="#fff"
          boxShadow="0px 1px 4px rgba(0, 0, 0, 0.08)"
          borderRadius={4}
          p={2}
          mb={2}
        >
                <Box fontSize={12} color="#4A4A4A" className="font-family-bold" mb={1}>Package {index + 1}: {value.package_name}
                  </Box>
                <Box
                border="1px solid #DADADA"
                className="input-oulined"
                pt={0.8}
                mb={2}
                borderRadius={4}
                bgcolor="#fff"
                fontSize={9}
              >
                <span>Value (%)</span>
                <TextField
                  id="standard-basic"
                  size="small"
                  defaultValue={value.commission}
                  type="number"
                  onBlur={onchange.bind(this,value)}
                  variant="outlined"
                  fullWidth
                />
              </Box>
              </Box>
              
              ))
            
           
         }
         
    
    {/* <Box
          bgcolor="#fff"
          boxShadow="0px 1px 4px rgba(0, 0, 0, 0.08)"
          borderRadius={4}
          p={2}
          mb={2}
        >
            <Box fontSize={12} color="#4A4A4A" className="font-family-bold" mb={1}>Slot Bookings</Box>
            <Box
           border="1px solid #DADADA"
           className="input-oulined"
           pt={0.8}
           mb={2}
           borderRadius={4}
           bgcolor="#fff"
           fontSize={9}
         >
           <span>Value (%)</span>
           <TextField
             id="standard-basic"
             size="small"
             defaultValue={20}
             type="number"
             variant="outlined"
             fullWidth
           />
         </Box>
    </Box> */}
                </Grid>
            </Grid>
            <Button className="btn-primary" >
          <Box px={3}>Save</Box>
        </Button>
        </div>
    )
}
