import BookBanner from '../BookBanner/BookBanner'
function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    "aria-controls": `vertical-tabpanel-${index}`,
  };
}

const styles = (theme) => ({
  root: {
    flexGrow: 1,
    display: "flex",
  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
  },
});

class EditListing extends Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
  };
  constructor(props) {
    super(props);
    this.state = {
      tabValue: 0,
      changeTab: false,
    };
  }
  onEdit = () => {
    this.setState({ changeTab: true });
  };
  handleChange = (event, newValue) => {
    this.setState({ tabValue: newValue });
  };
  ChangeSync = () => {
    this.setState({ changeTab: false });
  };
  componentDidMount=()=>{
    if(localStorage.getItem("status") !== "success"){
      this.props.history.push('./login')
    }
  }
  render() {
    const { classes } = this.props;
    return (
      <div className="body-bg">
        <SecondNav tabvalue={2}/>
        <Box mb={3} borderBottom="1px solid #D7D7D7" />
        <Container>
          <div className={classes.root}>
            <Tabs
              orientation="vertical"
              variant="scrollable"
              value={this.state.tabValue}
              onChange={this.handleChange}
              id="side-tabs"
              aria-label="Vertical tabs example"
              className={classes.tabs}
            >
              <Tab label="Notifications" {...a11yProps(0)} />
              <Tab label="Home Banner" {...a11yProps(1)} />
              <Tab label="Book Banner" {...a11yProps(2)} />
             
            </Tabs>
            <TabPanel
              className="tab-width"
              value={this.state.tabValue}
              index={0}
            >
            <Notifications />
            </TabPanel>
            <TabPanel
              className="tab-width"
              value={this.state.tabValue}
              index={1}
            >
            <HomeBanner />
            </TabPanel>
            <TabPanel
              className="tab-width"
              value={this.state.tabValue}
              index={2}
            >
            <BookBanner />
            </TabPanel>
         
          </div>
        </Container>
      </div>
    );
  }
}
export default withStyles(styles, { withTheme: true })(EditListing);
