import React,{Component} from 'react';
import clsx from 'clsx';
import {Tabs,Tab,Container,Box,Typography,Slider,Button,Select,Grid,Drawer,Radio,RadioGroup,MenuItem,InputLabel,FormControlLabel,FormControl,FormLabel, Divider, TextField} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Appbar from '../SecondNav/SecondNav';
import {withRouter} from 'react-router-dom';
import SortAutocomplete from '../_commen/sortAutocomplete';
import SubType from '../_commen/Subtype';
import SyncAltIcon from '@material-ui/icons/SyncAlt';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import TableServices from '../_commen/TableServices';
import Chip from '@material-ui/core/Chip';
import withWidth from '@material-ui/core/withWidth';
import Filter from '../assets/images/filter.png';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import {userServices} from '../_userServices/userServices'
import SearchIcon from '@material-ui/icons/Search';
import InputAdornment from '@material-ui/core/InputAdornment'
import IconButton from '@material-ui/core/IconButton'

  const useStyles = makeStyles((theme) => ({
    list: {
      width: 350,
      backgroundColor:"#F7F7F7"
    },
    fullList: {
      width: 'auto',
    },
    TextField:{
      '& .MuiOutlinedInput-root': {
      height:41,
      paddingRight:0,
      }
    }
  }));

const Services=(props)=> {
    const classes = useStyles();
    const [state, setState] = React.useState({
      right: false,
      selected:"",
    });

    const handleDelete = (value) => {
      props.deleteChip(value)
    };
  
    const handleClick = () => {
      console.info('You clicked the Chip.');
    };
  const onChange=(value)=>{
    props.onChange(value);
  }
  const onChangeRow=(value)=>{
    props.onChangeRow(value);
  }
  const selectData =(e)=>{
    setState({...state, selected:e.target.value})
    props.selectData(e.target.value)
  }
  const onClickSearch=()=>{
    props.selectData(state.selected)
  }
  const onFilter=()=>{
    setState({...state,right:false})
    props.onFilter();
  }
    const toggleDrawer = (anchor, open) => (event) => {
      if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
        return;
      }
  
      setState({ ...state, [anchor]: open });
    };
const onSort =(value)=>{
  props.onSort(value);
}
const onClickType=(value)=>{
  props.onClear(value);
}

    const list = (anchor) => (
      <div
        className={clsx(classes.list,"service-sidenav")}
        role="presentation"
        
        onKeyDown={toggleDrawer(anchor, false)}
      >
        <Box px={2} py={1}><Button className="btn-simple" onClick={toggleDrawer(anchor, false)}><KeyboardBackspaceIcon fontSize="small"/> back</Button></Box>
   
          <Box pt={2} bgcolor="#fff" borderRadius={10} p={2} mx={3}>
          
          <Box pt={1} fontSize={12} mb={2} color="#2c2c2c" className="font-family-bold">Service Type <span className="clear-sidenav" onClick={onClickType.bind(this,"sub_type")}>clear</span></Box>   
          <Box border="1px solid #878787" borderRadius={2}>
          <SubType data={props.data} onChange={onChange}/>
          </Box>
    <Divider />
   <Box pt={2} fontSize={12} mb={1} color="#2c2c2c" className="font-family-bold">Sync Type <span onClick={onClickType.bind(this,"type")} className="clear-sidenav">clear</span></Box>
      <FormControl component="fieldset">
      <RadioGroup aria-label="state" name="state" value={props.state.type} onChange={props.handleChangeType}>
        <FormControlLabel value="Charge" control={<Radio />} label="Charge" />
        <FormControlLabel value="Slot Sync" control={<Radio />} label="Slot Sync" />
        <FormControlLabel value="API" control={<Radio />} label="API" />
      </RadioGroup>
    </FormControl>
      <Box py={1}>
        <Divider />
        </Box>
        <Box pt={1} fontSize={12} mb={1} color="#2c2c2c" className="font-family-bold">Listing Type <span className="clear-sidenav" onClick={onClickType.bind(this,"Listing")}>clear</span></Box>
        <FormControl component="fieldset">
        
      <RadioGroup row aria-label="position" name="position" value={parseInt(props.state.visible)} onChange={props.onChangeVisible} >
      <Box mr={4} fontSize={12} mt={1.7}>Visible</Box>
        <FormControlLabel
          value={1}
          control={<Radio color="secondary" />}
          label="Yes"
          labelPlacement="end"
        />
        <FormControlLabel
          value={0}
          control={<Radio color="secondary" />}
          label="No"
          labelPlacement="end"
        />
      </RadioGroup>
    </FormControl>

    <FormControl component="fieldset">
      
      <RadioGroup row aria-label="position" name="position" value={parseInt(props.state.bookable)} onChange={props.onChangeBookable} >
      <Box mr={2.4} fontSize={12} mt={1.7}> Bookable </Box>
        <FormControlLabel
          value={1}
          control={<Radio color="secondary" />}
          label="Yes"
          labelPlacement="end"
        />
        <FormControlLabel
          value={0}
          control={<Radio color="secondary" />}
          label="No"
          labelPlacement="end"
        />
      </RadioGroup>
    </FormControl>
  
        <FormControl component="fieldset">
      
      <RadioGroup row aria-label="position" name="position" value={parseInt(props.state.featured)} onChange={props.onChangeFeatured}>
      <Box mr={2.7} fontSize={12} mt={1.7}>Featured</Box>
        <FormControlLabel
          value={1}
          control={<Radio color="secondary" />}
          label="Yes"
          labelPlacement="end"
        />
        <FormControlLabel
          value={0}
          control={<Radio color="secondary" />}
          label="No"
          labelPlacement="end"
        />
      </RadioGroup>
    </FormControl>
        <Box py={1}>
        <Divider />
        </Box>
        <Box pt={1} fontSize={12} mb={1} color="#2c2c2c" className="font-family-bold">Region <span className="clear-sidenav" onClick={onClickType.bind(this,"state")}>clear</span></Box>
   
      <FormControl component="fieldset">
      <RadioGroup aria-label="state" name="state" value={props.state.state} onChange={props.handleChangeState}>
        <FormControlLabel value="NCR" control={<Radio />} label="NCR" />
        <FormControlLabel value="Karnataka" control={<Radio />} label="Karnataka" />
        <FormControlLabel value="Telangana" control={<Radio />} label="Telangana" />
        <FormControlLabel value="ATamil" control={<Radio />} label="ATamil" />
        <FormControlLabel value="Kerala" control={<Radio />} label="Kerala" />
        
      </RadioGroup>
    </FormControl>
      <Box py={4}/>
    <Box position="fixed" width={270} bottom={0} pb={2} bgcolor="#fff">
    <Divider />
    
    <Box py={1}></Box>
    <Button className="btn-secondary" disabled={(props.state.state || props.state.type || props.state.featured || props.state.bookable || props.state.visible || props.state.sub_type) === ""} onClick={onFilter} size="large" fullWidth>Apply Filter</Button>
    </Box>
        </Box>
      
      </div>
    );
    return (
        <div className="body-bg box-input">
              <Appbar tabvalue={1}/>
              <Box py={1.5}/>
              <Container>
                  <Grid container spacing={1}>
                    <Grid item lg={8} md={7} sm={12}>
                        
                        <TextField id="outlined-basic"  InputProps={{
            endAdornment: <InputAdornment position="end"><IconButton onClick={onClickSearch}><SearchIcon /></IconButton></InputAdornment>,
          }} className={classes.TextField} onChange={selectData} value={props.data.keyword} placeholder="Search By Name" variant="outlined" size="small" fullWidth/>
                    </Grid>
                    <Grid item lg={3} md={3} sm={9}>
                    <SortAutocomplete data={props.data} onSort={onSort} />
                    </Grid>
                    <Grid item lg={1} md={2} sm={3}>
                        <Button variant="outlined" fullWidth className="btn-white" onClick={toggleDrawer('right', true)} color="inherit"><img src={Filter} width="16px" className="mr-2"/> Filter</Button>
                    </Grid>
                  </Grid>
                  {props.data.featured === "" && props.data.sort === "" && props.data.state === "" && props.data.type === "" && props.data.visible === "" && props.data.keyword.length < 3 && props.data.sub_type === "" ? <Box py={3}/>:
                  <Box y={2} my={2} fontSize={14}>Filter: 
                  {Object.keys(props.data).map((key,index)=>(
                    key === "data" || key === "loading" || key === "loading2" || key === "page" ? ""
                    :
                    props.data[key] === "" ? "" :
                    <Chip
                    key={index}
                    variant="outlined"
                    className="mx-1"
                    size="small"
                    label={`${key}: ${typeof props.data[key] === 'object' ? props.data[key].value : props.data[key].length === 1 ? props.data[key] === "1" ? "true" : "false" : props.data[key]}`}
                    onClick={handleClick}
                    
                    icon={<HighlightOffIcon onClick={handleDelete.bind(this,key)}/>}
                  />   
                  ))}
                  
     
      </Box>
}
                  <TableServices data={props.data} tableRow={onChangeRow} />
                  <Box py={2} />
              </Container>
              <div>
          <Drawer anchor={"right"} open={state["right"]} onClose={toggleDrawer('right', false)}>
            {list('right')}
          </Drawer>

    </div>
            
           
        </div>
    )
}



class App extends Component {
constructor(props){
  super(props);
  this.state={
    data:"",
    loading:true,
    loading2:false,
    bookable:"",
    visible:"",
    featured:"",
    state:"",
    type:"",
    sort:"",
    page:0,
    keyword:"",
    sub_type:"",
    is_active:"",
    noResult:"",
  }
}

  componentDidMount=()=>{
    if(localStorage.getItem("status") !== "success"){
      this.props.history.push('login')
    }
    this.setState({loading:true});
    userServices
    .services(this.state)
    .then((res)=>{
    if(res.success === 1){
      this.setState({data:res.data.service_data,loading:false})
    }
  })
  
  const self = this;
  window.addEventListener('scroll', function() {
    
    if(window.pageYOffset > 0){
    if(window.pageYOffset > (self.state.page + 1) * 200){
      alert("hello")
      self.setState({loading2:true,page:self.state.page + 1},
        ()=>(
          userServices
          .services(self.state,self.state.page)
          .then((res)=>{
          if(res.success === 1){
            self.setState({data:[...self.state.data,...res.data.service_data],loading2:false,noResult:res.data.service_data=== [] ? 'no result' : ''})
            
          }
        })
        )
        )
  }
}
})
  }
   onChangeBookable=(e)=>{
    this.setState({bookable:e.target.value})
  }
   onChangeVisible=(e)=>{
    this.setState({visible:e.target.value})
  }
   onChangeFeatured=(e)=>{
    this.setState({featured:e.target.value})
  }
     handleChangeState = (e) => {
      this.setState({state:e.target.value})
    };
     handleChangeType = (e) => {
      this.setState({type:e.target.value})
    };

  onFilter=()=>{
    this.setState({loading:true,});
    userServices
    .services(this.state,this.state.page)
    .then((res)=>{
    if(res.success === 1){
      this.setState({data:res.data.service_data,loading:false})
      
    }
  })
  }
  onChange=(value)=>{
    this.setState({sub_type:value});
      
}
onChangeRow =(value)=>{
  
  this.setState({loading:true,page:value});
  userServices
  .services(this.state,value)
  .then((res)=>{
  if(res.success === 1){
    this.setState({data:res.data.service_data,loading:false})
    
  }
})
}
selectData=(value)=>{
  this.setState({keyword:value});
  if(value !== undefined && value.length > 2){
    this.setState({loading:true},
      ()=>(
        userServices
        .services(this.state,this.state.page)
        .then((res)=>{
        if(res.success === 1){
          this.setState({data:res.data.service_data,loading:false})
          
        }
      })
      )
      );

}
}
onSort=(value)=>{
  this.setState({loading:true,sort:value},
    ()=>(
      userServices
      .services(this.state,this.state.page)
      .then((res)=>{
      if(res.success === 1){
        this.setState({data:res.data.service_data,loading:false})
        
      }
    })
    )
    );
 
}

deleteChip=(value)=>{
  this.setState({[value]:"",loading:true,},
  ()=>(
    userServices
    .services(this.state,this.state.page)
    .then((res)=>{
    if(res.success === 1){
      this.setState({data:res.data.service_data,loading:false})
    }
  })
  )
  );
}

onClear=(value)=>{
if(value === "Listing"){
  this.setState({bookable:"",visible:"",featured:"",})
}
else{
  this.setState({[value]:""})
}
}

  render() {    
    const data = this.state;
    const {
      scrollTop
    } = this.state
    const state = this.state
    console.log(state)
    const {handleChangeState,handleChangeType,onChangeFeatured,onChangeBookable,onChangeVisible} = this;
    return (
      <div >
        <Services data={data} onClear={this.onClear} deleteChip={this.deleteChip} onSort={this.onSort} onChange={this.onChange} selectData={this.selectData} onChangeRow={this.onChangeRow} onFilter={this.onFilter} state={state} handleChangeState={handleChangeState} handleChangeType={handleChangeType} onChangeFeatured={onChangeFeatured} onChangeBookable={onChangeBookable} onChangeVisible={onChangeVisible}/>
      </div>
    )
  }
}
export default withRouter(withWidth()(App));




