import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Box from '@material-ui/core/Box'
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import {userServices} from '../_userServices/userServices';
import CircularProgress from '@material-ui/core/CircularProgress';
import Alert from '@material-ui/lab/Alert';
import { withRouter } from 'react-router-dom';
const useStyles = makeStyles({
  list: {
    width: 300,
  },
  fullList: {
    width: 'auto',
  },
});

function TemporaryDrawer(props) {
  const classes = useStyles();
  const [state, setState] = React.useState({
    right: false,
    provider_name:"",
    service_name:"",
    loading:false,
    error:"",
  });
  console.log(state)
  const onChangeProvider=(e)=>{
    setState({...state,provider_name:e.target.value})
  }
  const onChangeService=(e)=>{
    setState({...state,service_name:e.target.value})
  }
  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };
  const onSubmit =(event)=>{
    event.preventDefault();
    setState({...state,loading:true})
    userServices
    .rename(props.search.id,state.service_name === "" ? props.search.service_name :state.service_name, state.provider_name === "" ? props.search.provider_name :state.provider_name,)
    .then((res)=>{
      setState({...state,provider_name:"",provider_name:"",right:false,error:"success"});
      setTimeout(function(){  setState({...state,error:"",right:false,provider_name:"",provider_name:"",});}, 3000);
      props.history.push({pathname:'/edit-listing',
      search:`${state.provider_name === "" ? props.search.provider_name :state.provider_name}/${state.service_name === "" ? props.search.service_name :state.service_name}/${props.search.id}`,
  }
      );
      window.location.reload(false)
    }
    )
  }

  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      
    >
        <Box bgcolor="#F7F7F7">
      <Box px={2} py={1}><Button className="btn-simple" onClick={toggleDrawer(anchor, false)}><KeyboardBackspaceIcon fontSize="small"/> back</Button></Box>
          <Box
        bgcolor="#fff"
        className="intext-input"
        fontSize={12}
        boxShadow="0px 1px 4px rgba(0, 0, 0, 0.08)"
        borderRadius={4}
        p={2}
        mx={2}
        mb={2}
      >
          <Box
          border="1px solid #DADADA"
          className="input-oulined"
          mb={2}
          pt={0.8}
          borderRadius={4}
          bgcolor="#fff"
          fontSize={9}
        >
            <span>Service Name</span>
          <TextField
            id="standard-basic"
            size="small"
            variant="outlined"
            placeholder="Service Name"
            onChange={onChangeService}
            fullWidth
            defaultValue={props.search.service_name}
          />
        </Box>
        <Box
          border="1px solid #DADADA"
          pt={0.8}
          borderRadius={4}
          bgcolor="#fff"
          fontSize={9}
          className="input-oulined"
        >
          <span>Provider Name</span>
          <TextField
            id="standard-basic"
            size="small"
            variant="outlined"
            placeholder="Service Name"
            defaultValue={props.search.provider_name}
            onChange={onChangeProvider}
            fullWidth
          />
        </Box>
        <Box fontSize={8} mt={1} color="#4A4A4A" className="font-family-regular">This will change the provider name across all services</Box>
        </Box>
        </Box>
        <Box mx={2}>
        { state.loading ? <CircularProgress color="secondary"/> : 
        <Button onClick={onSubmit} disabled={state.provider_name === "" &&  state.service_name === "" ? true : false } className="btn-primary">
        <Box px={3}>Save</Box>
      </Button>
}
      </Box>
      
    </div>
  );
  return (
    
      
        <React.Fragment >
          {
               state.error === "" ? "" :
               <Alert style={{width:300,top:30,zIndex:10000,position:"fixed",left:"50%",transform:"translateX(-50%)",boxShadow: "0px 0px 14px rgba(0, 0, 0, 0.05)"}} severity="success">{state.error}</Alert>
           }
          {
          props.props.width === 'xs' ?  <Button className="pull-right2 mt-6" onClick={toggleDrawer('right',true)}>
          <span>edit name</span>
        </Button>: <Button onClick={toggleDrawer('right',true)}>
              (<span>edit name</span>)
            </Button>
          }
       
          <Drawer anchor={'right'} open={state.right} onClose={toggleDrawer('right', false)}>
            {list('right')}
          </Drawer>
        </React.Fragment>
      
    
  );
}
export default withRouter(TemporaryDrawer)