class EditListing extends Component {
  static propTypes = {
    classes: PropTypes.object.isRequired,
  };
  constructor(props) {
    super(props);
    this.state = {
      tabValue: 0,
      changeTab: false,
      search:"",
      id:"",
      syncData:"",
    };
  }
  onEdit = () => {
    this.setState({ changeTab: true });
  };
  handleChange = (event, newValue) => {
    this.setState({ tabValue: newValue });
  };
  ChangeSync = () => {
    this.setState({ changeTab: false });
  };
componentDidMount=()=>{
  const split = this.props.location.search.split('/')
  const provider = split[0].split("%20")
  const service = split[1].split("%20")
  const provider_name = provider.join(" ").slice(1)
  const service_name = service.join(" ")
  const id = parseInt(split[2])
  this.setState({provider_name:provider_name,service_name:service_name,id:id})
  userServices
  .Sync(id)
  .then((res)=>{
  this.setState({ syncData:res })
  })
}
  render() {
    const { classes } = this.props;
    const {provider_name,service_name,syncData} = this.state;
    return (
      <div className="body-bg mob-listing">
        <Container>
        <Box pt={2}><Link to=""><ArrowBackIosIcon className="primary-color" fontSize="small"/></Link></Box>
         
          <Box  fontSize={14} className="breadcrum font-family-bold">
            {service_name}
    {/* <span> > {service_name} at {provider_name} </span> */}
            <DrawerName search={this.state} props={this.props} />
          </Box>
          <Box fontSize={12} mb={2} className="font-family-regular">5v5 Football</Box>
        </Container>
        <Box borderBottom="1px solid #D7D7D7">
            <Container>
        
            <Tabs
              value={this.state.tabValue}
              onChange={this.handleChange}
              indicatorColor="primary"
              textColor="primary"
              variant="scrollable"
              scrollButtons="auto"
              aria-label="scrollable auto tabs example"
              className="min-hight-mob"
            >
              <Tab label="Sync" {...a11yProps(0)} />
              <Tab label="Slots" {...a11yProps(1)} />
              <Tab label="Courts" {...a11yProps(2)} />
              <Tab label="Packages" {...a11yProps(3)} />
              <Tab label="Offers" {...a11yProps(4)} />
              <Tab label="Commissions" {...a11yProps(5)} />
              <Tab label="Featured" {...a11yProps(6)} />
              <Tab label="Photos" {...a11yProps(7)} />
            </Tabs>
            
            </Container>
        </Box>
        <Container>
          <div className={classes.root}>
            <TabPanel
              className="tab-widt2"
              value={this.state.tabValue}
              index={0}
            >
             {this.state.syncData === "" ? "" : this.state.syncData.data.sync_type === "Slot Sync" ? (
                <Sync classes={classes} syncData={syncData} onEdit={this.onEdit} />
              ) : (
                <EditSection classes={classes} syncData={syncData} ChangeSync={this.ChangeSync} />
              )}
            </TabPanel>
            <TabPanel
              className="tab-widt2"
              value={this.state.tabValue}
              index={1}
            >
              <Slots classes={classes} id={this.state.id} onEdit={this.onEdit} />
            </TabPanel>
            <TabPanel
              className="tab-widt2"
              value={this.state.tabValue}
              index={2}
            >
              <Courts />
            </TabPanel>
            <TabPanel
              className="tab-widt2"
              value={this.state.tabValue}
              index={3}
            >
              <Packages id={this.state.id}/>
            </TabPanel>
            <TabPanel
              className="tab-widt2"
              value={this.state.tabValue}
              index={4}
            >
              <Offers id={this.state.id}/>
            </TabPanel>
            <TabPanel
              className="tab-widt2"
              value={this.state.tabValue}
              index={5}
            >
              <Commissions id={this.state.id}/>
            </TabPanel>
            <TabPanel
              className="tab-widt2"
              value={this.state.tabValue}
              index={6}
            >
              <Featured id={this.state.id}/>
            </TabPanel>
            <TabPanel
              className="tab-widt2"
              value={this.state.tabValue}
              index={7}
            >
              <Photos />
            </TabPanel>
          </div>
        </Container>
      </div>
    );
  }
}
