import React,{useEffect} from 'react'
import Grid from '@material-ui/core/Grid'
import Box from '@material-ui/core/Box'
import TextField from '@material-ui/core/TextField'
import Radio from '@material-ui/core/Radio'
import FormControl from '@material-ui/core/FormControl'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import RadioGroup from '@material-ui/core/RadioGroup'
import Button from '@material-ui/core/Button'
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import MomentUtils from '@date-io/moment';
import { DatePicker } from "@material-ui/pickers";
import {userServices} from '../_userServices/userServices'
import Skeleton from '@material-ui/lab/Skeleton';

export default function Commissions(props) {
  const [state,setState] = React.useState({
    data:"",
    radio:"",
    start_date:"2019-12-20",
    end_date:"2019-12-20",
    loading:true,
    active:false
  })
  useEffect(() => {
    setState({...state,loading:true})
    userServices
    .featured(props.id)
    .then((res)=>(
      parseInt(res.data.is_featured) === 0 ? setState({...state,data:res,radio:res.data.is_featured,loading:false}):
      setState({...state,data:res,radio:res.data.is_featured,start_date:res.data.start_date,end_date:res.data.end_date,loading:false})
    ))
  },[]);
  const onChnageRadio =(e)=>{
    setState({...state,radio:e.target.value === "yes" ? 1 : 0,active:true })
  }
  const handleDateChange=(value,event)=>{
    setState({...state,[value]:event._d,active:true});
  }
  const onClick=()=>{
    setState({...state,loading:true});
    userServices
    .featuredUpdate(props.id,state)
    .then((res)=>(
      userServices
      .featured(props.id)
      .then((res)=>(
        setState({...state,data:res,radio:res.data.is_featured,start_date:res.data.start_date,end_date:res.data.end_date,loading:false,active:false})
      ))
    ));
  }
  const {
    radio,
    start_date,
    end_date,
    loading,
    data,
    active
  } = state
    return (
        <div>
            <Grid container>
                <Grid item xl={4} lg={4} md={4} sm={6} xs={12}>
                <Box fontSize={16} mb={2} className="font-family-bold">
          Featured
        </Box>
                <Box
          bgcolor="#fff"
          boxShadow="0px 1px 4px rgba(0, 0, 0, 0.08)"
          borderRadius={4}
          p={2}
          mb={2}
        >
            <Box fontSize={12} color="#4A4A4A" className="font-family-bold" mb={1}>Mark this service as featured?</Box>
            {
              loading === true ? 
              <>
              <Skeleton variant="rect" width={190} height={20} />
              <Box py={1}/>
              <Skeleton variant="rect" width={150} height={20} />
              <Box pt={1}/>
              <Skeleton variant="rect" width="100%" height={40} />
              <Box pt={1}/>
              <Skeleton variant="rect" width="100%" height={40} />
              <Box pt={1}/>
              </>
              :
              <>
              <FormControl component="fieldset">
        <RadioGroup row aria-label="position" value={data === "" ? "no" : radio === 1 ? "yes" : "no"} onChange={onChnageRadio} name="position" >
          <FormControlLabel
            value="yes"
            control={<Radio color="secondary" />}
            label="Yes"
            labelPlacement="end"
          />
          <FormControlLabel
            value="no"
            control={<Radio color="secondary" />}
            label="No"
            labelPlacement="end"
          />
        </RadioGroup>
      </FormControl>
      { data === "" ? "" : radio === 0 ? "" : 
      <>
      <Box
           border="1px solid #DADADA"
           className="input-oulined date-picker" 
           pt={0.8}
           mb={2}
           borderRadius={4}
           bgcolor="#fff"
           fontSize={9}
         >
           <span>Start Date</span>
          <Box >
            <MuiPickersUtilsProvider utils={MomentUtils}>
            <DatePicker
            autoOk={true}
        format="DD MMM YYYY"
        value={new Date(start_date)}
        onChange={handleDateChange.bind(this,"start_date")}
      />
              </MuiPickersUtilsProvider>
              </Box>
         </Box>
         <Box
           border="1px solid #DADADA"
           className="input-oulined date-picker" 
           pt={0.8}
           borderRadius={4}
           bgcolor="#fff"
           fontSize={9}
         >
           <span>End Date</span>
          <Box >
            <MuiPickersUtilsProvider utils={MomentUtils}>
              <DatePicker
              autoOk={true}
                format="DD MMM YYYY"
                value={new Date(end_date)}
                rightArrowButtonProps={<Button>cnksancks</Button>}
                onChange={handleDateChange.bind(this,"end_date")}
              />    
              </MuiPickersUtilsProvider>
              </Box>
         </Box>
         </>}
              </>
            }
            
    </Box>

                </Grid>
            </Grid>
            <Button disabled={!active} onClick={onClick} className="btn-primary" >
          <Box px={3}>Save</Box>
        </Button>
        </div>
    )
}
