import React,{Fragment} from 'react';
import './App.css';
import Appbar from './components/Appbar/Appbar';
import Login from './components/Login/Login';
import Services from './components/Services/Services';
import {HashRouter as Router,Switch,Route} from 'react-router-dom';
import EditListing from './components/EditingListing/EditListing';
import EditListingMobile from './components/EditingListing/EditListingMobile';
import Marketing from './components/Marketing/Marketing';
import MarketingMobile from './components/Marketing/MarketingMobile';
import Reports from './components/Reports/Reports';
import ReportsMobile from './components/Reports/ReportsMobile';
import withWidth from '@material-ui/core/withWidth';
import MobileServices from './components/Services/ServicesMobile'
function App(props) {
  return (
<Fragment>
    
    <Router>
    {props.width === 'xs' ? "" :<Appbar />}
      <Switch>
        <Route  path="/login" component={Login}/>
        <Route  path="/" exact component={props.width === 'xs' ? MobileServices: Services}/>
        <Route  path="/edit-listing" component={props.width === 'xs' ? EditListingMobile :EditListing}/>
        <Route path="/marketing" component={props.width === 'xs' ? MarketingMobile : Marketing} />
        <Route path="/reports" component={props.width === 'xs' ? ReportsMobile : Reports} />
      </Switch>
    </Router>
</Fragment>
  );
}

export default withWidth()(App);
