const URL_PREFIX = "http://staging.sportido.pro";

async function login(mobile,password) {
    const options = {
      headers: { 
        "Content-Type": "application/json",
      }
    };

  const { data: response } = await axios.post(
    URL_PREFIX + "/v1/dashboard/login", 
    {
      mobile:mobile,
      password:password
    },
    options
  );
  return response;

}

async function services(state,page) {
  const options = {
    headers: { 
      "Content-Type": "application/json",
    }
  };
const { data: response } = await axios.post(
  
  URL_PREFIX + "/v1/dashboard/services", 
    {
      "sort_by":  state.sort === "" ? "created_lates" : state.sort.value ,
      "sub_type":state.sub_type === "" ? undefined :state.sub_type.value ,
      "state_name": state.state === "" ? undefined :state.state,
      "sync_type": state.type === "" ? undefined :state.type,
      "is_partner":state.bookable === "" ? undefined :parseInt(state.bookable) ,
      "is_active": state.visible === "" ? undefined :parseInt(state.visible),
      "is_featured": state.featured === "" ? undefined :parseInt(state.featured),
      "keyword": state.keyword === "" ? undefined :state.keyword ,
      "page": page === undefined ? 1 : page + 1
    }
    
  ,options
);
return response;

}
