async function offersUpdate(id,filterData,package_id,condition) {
  const options = {
    headers: { 
      "Content-Type": "application/json",
    }
  };
  if(condition === true){
const { data: response } = await axios.post(
  URL_PREFIX + "/v1/vendor/offers/update", 
  {
    "service_id": `${id}`,
    "package_id":`${package_id}`,
    "rows":filterData
  },
  options
);
return response;
  }
  else{
    const { data: response } = await axios.post(
      URL_PREFIX + "/v1/vendor/offers/update", 
      {
        "service_id": `${id}`,
        "package_id":`${package_id}`,
        "rows":[filterData]
      },
      options
    );
    return response;
  }
}
