async function notification(title,body,tag,noti_type,loc_long,loc_lat,keyword,sport,distance,key1,key2,key3,value1,value2,value3) {
  const options = {
    headers: { 
      "Content-Type": "application/json",
    }
  };
  var newDate = new Date();
  newDate.setMonth(newDate.getMonth() - 3);
  const date = newDate.toISOString().slice(0,10)
const { data: response } = await axios.post(
  "http://engine.huddle.cc/push/notification", 
  {
    loc_lat:`${loc_lat}`,
    loc_long:`${loc_long}`,
    distance:distance,
    player_id:null,
    sport:sport === "Paintball" ? "167" : sport,
    login_within:date,
    noti_type:noti_type,
    key1:key1,
    key2:key2,
    key3:key3,
    value1:value1,
    value2:value2,
    value3:value3,
    title:title,
    body:body,
    device:"all",
    tag_group:tag,
    booked:"all"
  },
  options
  
);

return response;
}
