import React, { Component } from 'react'
import clsx from 'clsx';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { withRouter } from "react-router";
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import { Container,Box,FormControlLabel } from '@material-ui/core';
import Skeleton from '@material-ui/lab/Skeleton';
import Order from '../assets/images/order.png'
import Search from '../assets/images/Search.png'
import Whatsapp from '../assets/images/whatsapp.png'
import {Link} from 'react-router-dom';
import Switch from '@material-ui/core/Switch';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import Drawer from '@material-ui/core/Drawer';
import Logo from '../assets/images/logo.png'
import Admin from '../assets/images/admin.png'
import List from '@material-ui/core/List';
import Grow from '@material-ui/core/Grow';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import TextField from '@material-ui/core/TextField';
import SearchIcon from '@material-ui/icons/Search';
import InputAdornment from '@material-ui/core/InputAdornment';
import Alert from '@material-ui/lab/Alert';
import Slide from '@material-ui/core/Slide';
import {userServices} from '../_userServices/userServices'
import MobileFilter from './MobileFilter'
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    marginTop:15,
  },
  title: {
    flexGrow: 1,
  },
  appPadding:{
      paddingRight:0,
  },
  iconBtn:{
      marginRight:10,
  },
  appbar:{
    flexGrow: 1,
      borderRadius:4,
      boxShadow:"0px 0px 7px rgba(0, 0, 0, 0.05)",
  },
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
  navWidth:{
      width:"100%",
  },
  logout:{
      position:'absolute',
      bottom:20,
      fontSize:12,
      textTransform:'capitalize',
      fontFamily:'Montserrat-Regular!important'
  },
  appBar: {
    position: 'relative',
    width:"100%",
  },
  searchbar:{
    minWidth:"240px"
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

  const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
  });
const IOSSwitch = withStyles((theme) => ({
    root: {
      width: 42,
      height: 26,
      padding: 0,
      borderRadius:62,
      margin: theme.spacing(1),
    },
    switchBase: {
      padding: 1,
      '&$checked': {
        transform: 'translateX(16px)',
        color: theme.palette.common.white,
        '& + $track': {
          backgroundColor: '#98C28A',
          opacity: 1,
          border: 'none',
        },
      },
      '&$focusVisible $thumb': {
        color: '#98C28A',
        border: '6px solid #fff',
      },
    },
    
    thumb: {
      width: 24,
      height: 24,
    },
    track: {
      borderRadius: 26 / 2,
      border: `1px solid ${theme.palette.grey[400]}`,
    //   backgroundColor: theme.palette.grey[50],
      backgroundColor:"#C28D8A",
      opacity: 1,
      transition: theme.transitions.create(['background-color', 'border']),
    },
    checked: {},
    focusVisible: {},
  }))(({ classes, ...props }) => {
    return (
      <Switch
        focusVisibleClassName={classes.focusVisible}
        disableRipple
        classes={{
          root: classes.root,
          switchBase: classes.switchBase,
          thumb: classes.thumb,
          track: classes.track,
          checked: classes.checked,
        }}
        {...props}
      />
    );
  });

const Listings=(props)=> {
  const classes = useStyles();
  const [state, setState] = React.useState({
    left: false,
    modal:'',
  });
  const [radio,setRadio] = React.useState({
    visible:"",
    bookable:[],
    bookableStatus:[],
    alert:false,
  })
  const [page, setPage] = React.useState(0);
 
  const onChangeRadio=(value,e,val)=>{
    setState({...state, [value]:val})
  }
  const [open, setOpen] = React.useState(false);
  

  const handleClickOpen = () => {
    setOpen(true);
  };

  const onClickListing=(data)=>{
    props.props.history.push({pathname:'/edit-listing',
      search:`${data.provider_name}/${data.service_name}/${data.service_id}`,
      state:{
          ...data,
      }}
      )
    }
  const onChangeRadioVisible=(id,e,value)=>{
    userServices
    .visible(id,value)
    .then((res)=>{
    setRadio({...radio,visible:{
      ...radio.visible,
      [id]:value,
    },alert:true,});
    setTimeout(()=>{
      setRadio({...radio,visible:{
        ...radio.visible,
        [id]:value,
      },alert:false})
    },1000)
    })
  }
  const onChangeRadioBookable=(id,e,value)=>{
    userServices
    .bookable(id,value)
    .then((res)=>{
    setRadio({...radio,[id]:value ,alert:true,})
    setTimeout(()=>{
      setRadio({...radio,[id]:value ,
      alert:false})
    },1000)
    })
  }
  const handleChangePage = (event: unknown, newPage: number) => {
    props.tableRow(newPage)
    setPage(newPage);
  };

  const [selectedIndex, setSelectedIndex] = React.useState(1);

  const handleListItemClick = (event, index) => {
    setSelectedIndex(index);
    if(index === 2){
      props.props.history.push("/marketing")
    }
    if(index === 3){
      props.props.history.push("/reports")
    }
  };
  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };
  if(localStorage.getItem("status") !== "success"){
    props.props.history.push('./login')
  }
  const onChangeSearch =(e)=>{
    props.selectData(e.target.value);
  }
  const onClickSearch =()=>{
    setOpen(false);
    // props.selectData(state.selected);
  }
  const Logout =()=>{
    localStorage.removeItem("status")
    props.props.history.push("/login")
  }
  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onKeyDown={toggleDrawer(anchor, false)}
    >
<Box pl={2} pt={2} pb={1}>
    <img src={Logo} width={120}/>
    <div>
        <img src={Admin} width={70}/>
    </div>
</Box>
<Box pt={2} px={2} className="body-bg mob-sidenav">
 <List component="nav" aria-label="main mailbox folders">
        <ListItem
          button
          selected={selectedIndex === 0}
          onClick={(event) => handleListItemClick(event, 0)}
        >
          <ListItemText primary="My Follow-ups" />
          <ListItemIcon>
            <ChevronRightIcon />
          </ListItemIcon>
        </ListItem>
        <ListItem
          button
          selected={selectedIndex === 1}
          onClick={(event) => handleListItemClick(event, 1)}
        >
          <ListItemText primary="Services" />
          <ListItemIcon>
            <ChevronRightIcon />
          </ListItemIcon>
        </ListItem>
        <ListItem
          button
          selected={selectedIndex === 2}
          onClick={(event) => handleListItemClick(event, 2)}
        >
          <ListItemText primary="Marketing" />
          <ListItemIcon>
            <ChevronRightIcon />
          </ListItemIcon>
        </ListItem>
        <ListItem
          button
          selected={selectedIndex === 3}
          onClick={(event) => handleListItemClick(event, 3)}
        >
          <ListItemText primary="Reports" />
          <ListItemIcon>
            <ChevronRightIcon />
          </ListItemIcon>
        </ListItem>
        <ListItem
          button
          selected={selectedIndex === 4}
          onClick={(event) => handleListItemClick(event, 4)}
        >
          <ListItemText primary="Payouts" />
          <ListItemIcon>
            <ChevronRightIcon />
          </ListItemIcon>
        </ListItem>
        <ListItem
          button
          selected={selectedIndex === 5}
          onClick={(event) => handleListItemClick(event, 5)}
        >
          <ListItemText primary="Users" />
          <ListItemIcon>
            <ChevronRightIcon />
          </ListItemIcon>
        </ListItem>
      </List>
    
      <Button className={classes.logout} onClick={Logout}>Logout</Button>
      </Box>
    </div>
  );
  return (
      <div className="body-bg">
         {
               radio.alert === false ? "" :
           <Alert style={{width:300,top:30,zIndex:10000,position:"fixed",left:"50%",transform:"translateX(-50%)",boxShadow: "0px 0px 14px rgba(0, 0, 0, 0.05)"}} severity="success">Success</Alert>
           }
    <div className={classes.root}>
        <Container>
      <AppBar position="static" color="inherit" className={classes.appbar}>
        <Toolbar className={classes.appPadding}>
          <IconButton edge="start" onClick={toggleDrawer('left', true)} className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton>
          {
            open === true ? 
            <Grow
          in={open === true}
          style={{ transformOrigin: '0 0 0' }}
          {...(open === true ? { timeout: 1000 } : {})}
        >
            <TextField onChange={onChangeSearch} id="outlined-basic"  InputProps={{
              endAdornment: <InputAdornment position="end"><IconButton onClick={onClickSearch}><SearchIcon /></IconButton></InputAdornment>,
            }} className={classes.TextField}  placeholder="Search By Name" variant="outlined" size="small" fullWidth/>
            </Grow>
            :
          <>
            <Typography variant="h6" className={classes.title}>
          Services
          </Typography>
          <IconButton onClick={handleClickOpen} className={classes.iconBtn} aria-label="delete" size="small">
          <img src={Search} width="16px"/>
        </IconButton>
        </>
          }
          <span className={open === true ? "ml10":"" }>
          <MobileFilter state={props.state} onFilter={props.onFilter} onChange={(value)=>
  props.onChange(value)} onClear={(value)=>props.onClear(value)} onClickType={(value)=>props.onClear(value)} onChangeFeatured={(value)=>props.onChangeFeatured(value)} handleChangeState={(value)=>props.handleChangeState(value)} handleChangeType={(value)=>props.handleChangeType(value)} onChangeVisible={(value)=>props.onChangeVisible(value)} onChangeBookable={(value)=>props.onChangeBookable(value)}/>
          </span>
        <IconButton className={classes.iconBtn} aria-label="delete" size="small">
          <img src={Order} width="16px"/>
        </IconButton>
        </Toolbar>
      </AppBar>
      </Container>
    </div>
    <Box my={2} />
    <Container>
        <Box p={2} boxShadow="0px 6px 25px rgba(0, 0, 0, 0.08)" bgcolor="#fff" borderRadius={6}>
            {
                props.data.loading === true ? 
                [1,2,3,4,5].map((value,index)=>(
                <div key={index}>
                <Skeleton variant="rect" height={17} />
                <Box pt={0.5}/>
                <Skeleton variant="rect" height={17} />
                <Box pt={0.5}/>
                <Skeleton variant="rect" height={17} />
                <Box pt={0.5}/>
                <Skeleton variant="rect" height={17} />
                <Box py={2}>
                <Skeleton variant="rect" height={1} />
                </Box>
                </div>
                ))
                : props.data.data.length === 0 ? <Box>No Data Found</Box> :props.data.data.map((value)=>(
                    <>
            <Box fontSize={12} className="font-family-bold">{value.provider_name}<Link className="float-right" to=""><img src={Whatsapp} width={14}/></Link></Box>
            <Box fontSize={12} mt={1} className="font-family-regular">{value.service_name} <span className="float-right">{value.sync_type}</span></Box>
            <Box fontSize={12} mt={1} className="font-family-regular"><FormControlLabel className="form-margin"
        control={<IOSSwitch name="checkedA" onChange={onChangeRadioVisible.bind(this,value.service_id)} checked={radio.visible[value.service_id] === null || radio.visible[value.service_id] === undefined ? value.is_partner === 1 ? true : false :  radio.visible[value.service_id] } />}
                />Visible <span className="float-right padding2">{value.last_hit_sync === null ? "-" : value.last_hit_sync }</span></Box>
       <Box fontSize={12} mt={1} className="font-family-regular"><FormControlLabel className="form-margin"
        control={<IOSSwitch name="checkedB" onChange={onChangeRadioBookable.bind(this,value.service_id)} checked={radio[value.service_id] === null || radio[value.service_id] === undefined ? value.is_partner === 1 ? true : false :  radio[value.service_id] }/>}
      />Bookable <a onClick={onClickListing.bind(this,value)} className="float-right"><ChevronRightIcon  fontSize="small"/></a></Box>
      <Box my={2} border="1px solid #EFEFEF"/>
      </>
      ))}
        </Box>
        {['left'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Drawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
        <div>
    </div>
    </Container>
    </div>
  );
}


class Services extends Component {
  constructor(props){
    super(props);
    this.state={
      data:"",
      loading:true,
      bookable:"",
      visible:"",
      featured:"",
      state:"",
      type:"",
      sort:"",
      page:0,
    }
  }
  
    componentDidMount=()=>{
      if(localStorage.getItem("status") !== "success"){
        this.props.history.push('login')
      }
      this.setState({loading:true});
      userServices
      .services(this.state)
      .then((res)=>{
      if(res.success === 1){
        this.setState({data:res.data.service_data,loading:false})
      }
    })
    }
   
     onChangeBookable=(e)=>{
      this.setState({bookable:e.target.value})
    }
     onChangeVisible=(e)=>{
      this.setState({visible:e.target.value})
    }
     onChangeFeatured=(e)=>{
      this.setState({featured:e.target.value})
    }
       handleChangeState = (e) => {
        this.setState({state:e.target.value})
      };
       handleChangeType = (e) => {
        this.setState({type:e.target.value})
      };
  
    onFilter=()=>{
      this.setState({loading:true,});
      userServices
      .services(this.state)
      .then((res)=>{
      if(res.success === 1){
        this.setState({data:res.data.service_data,loading:false})
        
      }
    })
    }
    onChange=(value)=>{
    
  }
  onChangeRow =(value)=>{
    this.setState({loading:true,page:value});
    userServices
    .services(this.state,value)
    .then((res)=>{
    if(res.success === 1){
      this.setState({data:res.data.service_data,loading:false})
      
    }
  })
  }
  selectData=(value)=>{
    this.setState({keyword:value});
  if(value !== undefined && value.length > 2){
    this.setState({loading:true});
  userServices
  .searchService(this.state,this.state.page,value)
  .then((res)=>{
  if(res.success === 1){
    this.setState({data:res.data.service_data,loading:false})
    
  }
})
}
  }
  onSort=(value)=>{
    this.setState({loading:true,sort:value.value});
    userServices
    .services(this.state,this.state.page,this.state.keyword,value.value)
    .then((res)=>{
    if(res.success === 1){
      this.setState({data:res.data.service_data,loading:false})
      
    }
  })
  }
  deleteChip=(value)=>{
    this.setState({[value]:"",loading:true,},
    ()=>(
      userServices
      .services(this.state,this.state.page,this.state.keyword,value.value)
      .then((res)=>{
      if(res.success === 1){
        this.setState({data:res.data.service_data,loading:false})
      }
    })
    )
    );
    
  }
  onClear=(value)=>{
  if(value === "Listing"){
    this.setState({bookable:"",visible:"",featured:"",})
  }
  else{
    this.setState({[value]:""})
  }
  }
  render() {
    const data = this.state;
    const {
      scrollTop
    } = this.state
    const state = this.state
    const {handleChangeState,handleChangeType,onChangeFeatured,onChangeBookable,onChangeVisible} = this;
    return (
      <div className="mobile-service">
       <Listings data={data} onClear={this.onClear} deleteChip={this.deleteChip} onSort={this.onSort} onChange={this.onChange} selectData={this.selectData} onChangeRow={this.onChangeRow} onFilter={this.onFilter} state={state} handleChangeState={handleChangeState} handleChangeType={handleChangeType} onChangeFeatured={onChangeFeatured} onChangeBookable={onChangeBookable} props={this.props} onChangeVisible={onChangeVisible}/>
      <Container fixed>
      <Box textAlign="right" my={2}>
        <Button variant="contained" color="secondary" className="btn-secondary">See More</Button>
      </Box>
      </Container>
      </div>
    )
  }
}
export default withRouter(Services);
