import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Collapse from '@material-ui/core/Collapse';
import IconButton from '@material-ui/core/IconButton';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';

const useRowStyles = makeStyles({
  border:{
  borderBottom:'1px solid #eee!important',
  }
});

function createData(name, calories, fat, carbs, protein, price) {
  return {
    name,
    calories,
    fat,
    carbs,
    protein,
    price,
    history: [
      { date: '2020-01-05', customerId: '11091700', amount: 3 },
      { date: '2020-01-02', customerId: 'Anonymous', amount: 1 },
    ],
  };
}

function Row(props) {
  const { row } = props;
  const [open, setOpen] = React.useState(false);
  const classes = useRowStyles();

  return (
    <React.Fragment>
      <TableRow className={classes.border}>
        <TableCell component="th" scope="row" className="fomt-family-bold">
          {row.name}
        </TableCell>
        <TableCell align="left">1778 (<span className="success-color">33%</span>)</TableCell>
        <TableCell align="left">1778 (<span className="success-color">33%</span>)</TableCell>
        <TableCell align="left">1778 (<span className="success-color">33%</span>)</TableCell>
        <TableCell align="left">1778 (<span className="success-color">33%</span>)</TableCell>
        <TableCell align="left">1778 (<span className="success-color">33%</span>)</TableCell>
        <TableCell align="left">1778 (<span className="success-color">33%</span>)</TableCell>
        <TableCell>
          <IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
            {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
          </IconButton>
        </TableCell>
      </TableRow>
      <TableRow className={classes.border}>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={8}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box margin={1}>
              <Table size="small" aria-label="purchases">
                <TableBody>
                    <TableRow>
                      <TableCell />
                      <TableCell />
                      <TableCell component="th" align="right" className="tabel-h">Day</TableCell>
                      <TableCell component="th" >78 (<span className="success-color">33%</span>)</TableCell>
                      <TableCell component="th" >78 (<span className="success-color">33%</span>)</TableCell>
                      <TableCell component="th" >78 (<span className="success-color">33%</span>)</TableCell>
                      <TableCell component="th" >78 (<span className="success-color">33%</span>)</TableCell>
                      <TableCell />
                    </TableRow>
                    <TableRow>
                    <TableCell />
                    <TableCell />
                      <TableCell component="th" align="right" className="tabel-h">Month</TableCell>
                      <TableCell component="th" >78 (<span className="success-color">33%</span>)</TableCell>
                      <TableCell component="th" >78 (<span className="success-color">33%</span>)</TableCell>
                      <TableCell component="th" >78 (<span className="success-color">33%</span>)</TableCell>
                      <TableCell component="th" >78 (<span className="success-color">33%</span>)</TableCell>
                      <TableCell />
                    </TableRow>
                    <TableRow>
                    <TableCell />
                    <TableCell />
                      <TableCell component="th" align="right" className="tabel-h">Year</TableCell>
                      <TableCell component="th" >78 (<span className="success-color">33%</span>)</TableCell>
                      <TableCell component="th" >78 (<span className="success-color">33%</span>)</TableCell>
                      <TableCell component="th" >78 (<span className="success-color">33%</span>)</TableCell>
                      <TableCell component="th" >78 (<span className="success-color">33%</span>)</TableCell>
                      <TableCell />
                    </TableRow>
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}

Row.propTypes = {
  row: PropTypes.shape({
    calories: PropTypes.number.isRequired,
    carbs: PropTypes.number.isRequired,
    fat: PropTypes.number.isRequired,
    history: PropTypes.arrayOf(
      PropTypes.shape({
        amount: PropTypes.number.isRequired,
        customerId: PropTypes.string.isRequired,
        date: PropTypes.string.isRequired,
      }),
    ).isRequired,
    name: PropTypes.string.isRequired,
    price: PropTypes.number.isRequired,
    protein: PropTypes.number.isRequired,
  }).isRequired,
};

const rows = [
  createData('Frozen yoghurt', 159, 6.0, 24, 4.0, 3.99),
  createData('Ice cream sandwich', 237, 9.0, 37, 4.3, 4.99),
  createData('Eclair', 262, 16.0, 24, 6.0, 3.79),
  createData('Cupcake', 305, 3.7, 67, 4.3, 2.5),
  createData('Gingerbread', 356, 16.0, 49, 3.9, 1.5),
];

export default function CollapsibleTable() {
  const classes = useRowStyles();
  return (
    <TableContainer component={Paper}>
      <Table aria-label="collapsible table">
        <TableHead>
          <TableRow className={classes.border}>
            <TableCell>PROVIDER NAME</TableCell>
            <TableCell align="left">SERVICE</TableCell>
            <TableCell align="left">CITY</TableCell>
            <TableCell align="left">BOOKING</TableCell>
            <TableCell align="left">GMV</TableCell>
            <TableCell align="left">REVENUE</TableCell>
            <TableCell align="left">P/L</TableCell>
            <TableCell align="left"></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <Row key={row.name} row={row} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
