import React from 'react';
import Select from 'react-select';
 
const options = [
  { value: 'created_latest', label: 'Created latest' },
  { value: 'created_old', label: 'Created old' },
  { value: 'last_hit_email', label: 'Last hit email' },
  { value: 'last_hit_sms', label: 'Last hit sms' },
  { value: 'last_hit_sync', label: 'Last hit sync' },
];

export default class App extends React.Component {
  constructor(props){
    super(props)
  }
  state = {
    selectedOption: null,
  };
  handleChange = selectedOption => {
    // this.setState({ selectedOption });
    this.props.onSort(selectedOption);
  };
  render() {
    const { selectedOption } = this.state;
    
    return (
      <Select
        value={this.props.data.sort}
        onChange={this.handleChange}
        options={options}
        placeholder="Sort by: Last Hit (Oldest)"
      />
    );
  }
}