import React from 'react';
import clsx from 'clsx';
import { Container,TextField,Grid,Box,Button } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles';
import AdminLogo from '../assets/images/adminicon.png';
import { red } from '@material-ui/core/colors';
import {Link,withRouter} from 'react-router-dom';
import { userServices } from '../_userServices/userServices'
import Alert from '@material-ui/lab/Alert';
import CircularProgress from '@material-ui/core/CircularProgress';
const useStyles = makeStyles((theme) => ({
    Form:{
        margin:"0 auto",
        marginTop:30,
        background: "#FFFFFF",
        boxShadow:`0px 1px 4px rgba(0, 0, 0, 0.33)`,
        padding:30,
        textAlign:'center'
    },
    TextField:{
        marginTop:15,
    },
    btnPrimary:{
        marginTop:40,
        marginBottom:50,
        padding:"8px 40px!important",
    }
}));

function Login(props) {
    const classes = useStyles();
    const [state,setState] = React.useState({
        mobile:"",
        password:"",
        loading:false,
        error:"",
    })
    if(localStorage.getItem("status") === "success"){
        props.history.push('./')
      }
      const onChangeMobile=(e)=>{
        setState({...state,mobile:e.target.value})
      }
      
      const onChangePassword=(e)=>{
        setState({...state,password:e.target.value})
      }
    const onSubmit =()=>{
    const {mobile,password} = state;
    if(state.mobile === "" || state.password === ""){
        setState({...state,loading:false,error:"Some fields are empty"});
        setTimeout(function(){  setState({...state,error:""});}, 1500);
    }
    else{
        setState({...state,loading:true});
        userServices
        .login(mobile,password)
        .then((res)=>{
    if(res.success === 1 ){
        setState({...state,loading:false});
        localStorage.setItem("status", "success");
        props.history.push('./');
    }
    else{
        setState({...state,loading:false,error:res.message});
        setTimeout(function(){  setState({...state,error:""});}, 3000);
    }
})
    }
}

    return (
        <div className="body-bg login">
           <Container>
               <Grid container className="box-input login">
                   <Grid item lg={4} className={classes.Form}>
                       <img src={AdminLogo} width={136}/>
                        <Box borderBottom="1px solid #EFEFEF" my={2} />
                            <TextField id="outlined-basic" onBlur={onChangeMobile} className={classes.TextField} placeholder="Mobile" variant="outlined" fullWidth/>
                            <TextField id="outlined" type="password" onChange={onChangePassword} className={classes.TextField} placeholder="Password" variant="outlined" fullWidth/>
                 {
                     state.loading === true ?
                     <>
                     <Box mt={4}/>
                     <CircularProgress color="secondary"/>
                     </>
                     :
                     <Button variant="contained" disabled={state.mobile.length > 5 && state.password.length > 5 ? false :true  } onClick={onSubmit} size="large" className={clsx(classes.btnPrimary,"btn-primary")}>Login</Button>
                 }
                   
                   </Grid>
               </Grid>
           </Container>
           {
               state.error === "" ? "" :
           <Alert style={{width:300,top:30,zIndex:10000,position:"fixed",left:"50%",transform:"translateX(-50%)",boxShadow: "0px 0px 14px rgba(0, 0, 0, 0.05)"}} severity="error">{state.error}</Alert>
           }
           
        </div>
    )
}
export default withRouter(Login);
